// Game3.jsx
import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import alertSound from '../../assets/alert.mp3'; // 引入音效文件
import './Game3.scss'; // 導入 SCSS 文件

const Game3 = () => {
  const [step, setStep] = useState(1); // 1: 初始提示, 2: 找到碎片, 3: 拼好了, 4: 輸入關鍵字
  const [inputValue, setInputValue] = useState('');
  const [isCorrect, setIsCorrect] = useState(false);
  const [audioPlayed, setAudioPlayed] = useState(false); // 音效是否已播放
  const navigate = useNavigate();
  const audioRef = useRef(null);

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
    if (e.target.value === '二次世界大戰') {
      setIsCorrect(true);
    } else {
      setIsCorrect(false);
    }
  };

  const handleEndGame = () => {
    if (audioRef.current) {
      audioRef.current.pause(); // 停止播放音效
      audioRef.current.currentTime = 0; // 重置音頻時間
    }
    navigate('/', { state: { dialogIndex: 21 } });
  };

  const handleButtonClick = () => {
    if (!audioPlayed) {
      audioRef.current.play().catch((error) => {
        console.error('Audio playback was prevented:', error);
      });
      setAudioPlayed(true);
    }
    setStep(step + 1);
  };

  useEffect(() => {
    audioRef.current = new Audio(alertSound); // 使用引入的音效文件
    audioRef.current.loop = true; // 設定音頻循環播放

    // 在組件卸載時停止音效
    return () => {
      if (audioRef.current) {
        audioRef.current.pause(); // 停止播放音效
        audioRef.current.currentTime = 0; // 重置音頻時間
      }
    };
  }, []);

  return (
    <div className="container">
      <div style={{ display: 'none' }}>
        <audio ref={audioRef} src={alertSound} loop />
      </div>
      {step === 1 && (
        <div className="content">
          <p>小朋友們! 快到防空洞旁邊仔細找找看，有沒有甚麼解除警報的方式</p>
          <button className="button" onClick={handleButtonClick}>找到碎片了</button>
        </div>
      )}
      {step === 2 && (
        <div className="content">
          <p>將找到的碎片拼拼看吧!</p>
          <button className="button" onClick={handleButtonClick}>拼好了</button>
        </div>
      )}
      {step === 3 && (
        <div className="content">
          <input
            type="text"
            placeholder="請輸入答案"
            value={inputValue}
            onChange={handleInputChange}
            className="input"
          />
          {isCorrect && (
            <button className="button" onClick={handleEndGame}>遊戲結束</button>
          )}
        </div>
      )}
    </div>
  );
};

export default Game3;
