// AR版嘗試






import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { RetweetOutlined } from '@ant-design/icons'; // 引入 Ant Design 的圖標
import mapImageFront from '../../assets/地圖正面.jpg'; // 引入地圖正面圖片
import mapImageBack from '../../assets/地圖反面.jpg'; // 引入地圖反面圖片
import audio1 from '../../assets/第一段.mp3'; // 引入音效文件1
import audio2 from '../../assets/第二段.mp3'; // 引入音效文件2
import audio3 from '../../assets/第三段.mp3'; // 引入音效文件3
import './Game4.scss'; // 導入 SCSS 文件

const Game4 = () => {
  const [inputValue, setInputValue] = useState(''); // 輸入框的值
  const [showEndButton, setShowEndButton] = useState(false); // 控制顯示遊戲結束按鈕
  const [isFront, setIsFront] = useState(true); // 狀態：顯示正面還是反面地圖
  const navigate = useNavigate();

  // 播放錄音
  const handlePlayAudio = (index) => {
    const audioFiles = [audio1, audio2, audio3]; // 音頻文件數組
    const audio = new Audio(audioFiles[index]);
    audio.play();

    // 如果點擊的是第二個音頻，檢查輸入框值並顯示遊戲結束按鈕
    if (index === 1) {
      checkInputValue();
    }
  };

  // 檢查輸入框的值
  const checkInputValue = () => {
    if (inputValue === '手水舍') {
      setShowEndButton(true); // 顯示遊戲結束按鈕
    }
  };

  // 結束遊戲
  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 38 } });
  };

  // 清除結果和已播放的音頻
  const clearResultAndAudio = () => {
    setInputValue(''); // 清空輸入框
    setShowEndButton(false); // 隱藏遊戲結束按鈕
  };

  // 切換地圖
  const toggleMap = () => {
    setIsFront(!isFront); // 切換地圖正反面
  };

  return (
    <div className="container">
      <div className="mapContainer">
        {/* 地圖背景圖片 */}
        <img src={isFront ? mapImageFront : mapImageBack} alt="地圖圖片" className="mapImage" />
        {/* 切換地圖按鈕在地圖右邊 */}
        <button className="map-toggle-button" onClick={toggleMap}>
          <RetweetOutlined />
        </button>
      </div>
      <div className="buttonsContainer">
        {/* 播放錄音按鈕 */}
        <button className="audio-button" onClick={() => handlePlayAudio(0)}>
          播放錄音 1
        </button>
        <button className="audio-button" onClick={() => handlePlayAudio(1)}>
          播放錄音 2
        </button>
        <button className="audio-button" onClick={() => handlePlayAudio(2)}>
          播放錄音 3
        </button>
      </div>
      <div className="resultContainer">
        {/* 結果顯示區域 */}
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onBlur={checkInputValue} // 當輸入框失去焦點時檢查值
          placeholder="請輸入關鍵字"
          className="input"
        />
        {showEndButton && (
          <div>
            {/* 結果為特定值時顯示的內容 */}
            <button className="end-button" onClick={handleEndGame}>
              遊戲結束
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Game4;

