// 介面會有一小小樹蛙神一隻在神社中心位置。

import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Game = () => {
    const navigate = useNavigate();

    const handleEndGame = () => {
        navigate('/', { state: { dialogIndex: 47 } });
    };



    return (
        <div>
            <button onClick={handleEndGame}>遊戲結束</button>

        </div>
    );
};

export default Game;
