//相機轉動可以看到各種小小樹蛙神（跟寶可夢一樣）。
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Game = () => {
    const navigate = useNavigate();

    const handleEndGame = () => {
        navigate('/', { state: { dialogIndex: 67 } });
    };


    return (
        <div>
            <button onClick={handleEndGame}>遊戲結束</button>
        </div>
    );
};

export default Game;
