import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './Game1.scss';

const Game1 = () => {
  // 狀態變量，跟蹤 AR 資源是否已加載和標記是否找到
  const [isLoaded, setIsLoaded] = useState(false);
  const [markerFound, setMarkerFound] = useState(false);
  const navigate = useNavigate();

  // 確認 AFRAME 已加載，然後設置 isLoaded 為 true
  useEffect(() => {
    const interval = setInterval(() => {
      if (window.AFRAME?.registerComponent) {
        setIsLoaded(true);
        console.log('AFRAME loaded');
        clearInterval(interval);
      }
    }, 100);

    return () => clearInterval(interval);
  }, []);

  // 加載時隱藏背景
  useEffect(() => {
    document.body.classList.add('hide-background');
    return () => {
      document.body.classList.remove('hide-background');
    };
  }, []);

  // 在 AFRAME 資源加載後設置 AR 標記監聽器
  useEffect(() => {
    if (!isLoaded) return;

    const marker = document.querySelector('a-marker');
    if (!marker) {
      console.log('Marker not found');
      return;
    }

    // 當找到標記時設置 markerFound 為 true，丟失標記時設置為 false
    const handleMarkerFound = () => {
      console.log('Marker found');
      setMarkerFound(true);
    };
    const handleMarkerLost = () => {
      console.log('Marker lost');
      setMarkerFound(false);
    };

    marker.addEventListener('markerFound', handleMarkerFound);
    marker.addEventListener('markerLost', handleMarkerLost);

    return () => {
      marker.removeEventListener('markerFound', handleMarkerFound);
      marker.removeEventListener('markerLost', handleMarkerLost);
    };
  }, [isLoaded]);

  // 遊戲結束處理程序，停止 AR 相機並導航回首頁
  const handleEndGame = () => {
    const arVideo = document.getElementById('arjs-video');
    if (arVideo) {
      arVideo.style.display = 'none';
    }

    stopARCamera(() => {
      document.body.classList.remove('hide-background');
      navigate('/', { state: { dialogIndex: 5 } });
    });
  };

  // 停止 AR 相機的函數
  const stopARCamera = (callback) => {
    const scene = document.querySelector('a-scene');
    if (!scene) {
      callback();
      return;
    }

    const arjsSystem = scene.systems['arjs'];
    if (arjsSystem && arjsSystem.source) {
      arjsSystem.source.stop();

      const cameraEntity = scene.querySelector('[camera]');
      if (cameraEntity) {
        cameraEntity.parentNode.removeChild(cameraEntity);
      }

      arjsSystem.dispose();

      while (scene.firstChild) {
        scene.removeChild(scene.firstChild);
      }

      scene.parentNode.removeChild(scene);
      setTimeout(callback, 100);
    } else {
      callback();
    }
  };

  // 啟動 AR 相機的函數
  const startARCamera = () => {
    const arjsSystem = document.querySelector('a-scene')?.systems['arjs'];
    if (arjsSystem && arjsSystem.source) {
      arjsSystem.source.start();
    }
  };

  // 在 AR 資源加載後啟動 AR 相機，並在組件卸載時停止 AR 相機
  useEffect(() => {
    if (isLoaded) {
      startARCamera();
    }

    return () => {
      stopARCamera(() => { });
    };
  }, [isLoaded]);

  return (
    <div className="container1">
      {isLoaded ? (
        // AR 場景，嵌入模式和使用 WebCam
        <a-scene embedded arjs="sourceType: webcam;">
          {/* 使用 Hiro 預設的 AR 標記 */}
          <a-marker preset="hiro" emitevents="true"></a-marker>
        </a-scene>
      ) : (
        // 資源加載期間顯示 Loading AR...
        <p>Loading AR...</p>
      )}
      {/* 如果找到標記，顯示結束遊戲按鈕 */}
      {markerFound && (
        <div className="button-container">
          <button className="end-button" onClick={handleEndGame}>
            遊戲結束
          </button>
        </div>
      )}
    </div>
  );
};

export default Game1;
